body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #333533;
  color: #E8EDDF;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
}

.Main {
    width: stretch;
    align-self: center;
    max-width: 1000px;
    height: 70%;
    display: flex;
    justify-content: space-around;
}

.portfolio {
    border-radius: 20px;
    margin: 20px 0;
    margin-left: 20px;
    width: 30%;
    background-color: #242423;
    max-width: 800px;
    border: 2px outset #F5CB5C;
    overflow-y: auto;
}

.maincontent {
    width: 70%;
    background-color: #CFDBD5;
    border-radius: 20px;
    margin: 20px;
    display: flex;
    border: 2px solid black;
    
}

.maincontent-inner {
    background-color: #333533;
    border-radius: 20px;
    margin: 20px;
    display: flex;
    flex-direction: column;
    width: stretch;
    padding: 20px;
    border: 2px solid black;
}

.maincontent-title {
    display: flex;
    justify-content: center;
    gap: 10px;
}

.maincontent-body {
    text-align: left;
    overflow-y: auto;
}

.maincontent-img {
    width: stretch;
    max-width: 300px;
    float: right;
    margin: 10px;
}

.maincontent-text {
    margin: 10px;
}

.projectlink {
    border-radius: 10px;
    background-color: #333533;
    height: 150px;
    margin: 10px;
}
.projectlink:hover {
    background-color: #CFDBD5;
    cursor: pointer;
    color: #333533;
}
.projectlink > img {
    margin: 0 5px;
    width: stretch;
    height: stretch;
    max-width: 175px;
    max-height: 100px;
}


.header {
    width: stretch;
    align-self: center;
    max-width: 1000px;
    border-radius: 20px;
    margin: 20px;
    height: 75px;
    background-color: #242423;
    border: 2px outset #F5CB5C;
    text-align: left;
}
.header > h2 {
    margin-left: 50px;
    border-radius: 15px;
    padding: 5px;
    width: fit-content;
}
.header > h2:hover {
    background-color: #CFDBD5;
    cursor: pointer;
    color: #333533;
}

.footer {
    background-color: #242423;
    height: 100px;
}

.footer-react {
    margin-right: 20px;
    display: flex;
    align-items: center;
    justify-content: right;
}
.footer-react > img {
    width: 50px;
    height: 50px;
}